.calci{
    margin:0px;
    width: calc(100vw - 0px);
    background-color: antiquewhite;
    height: 90.5vh;
    display: flex;
    align-items: end;
    text-align: center;
    justify-content: center;
    flex-wrap: wrap;
}
.container{
    background-color: black;
    border:1px solid black;
    max-width: 99vw;
    text-align: center;
    display: grid;
    /* grid-template-rows: repeat(5,calc(50vh/5));
    grid-template-columns: repeat(4,calc(97vw/4)); */
    grid-template-rows: repeat(5,calc(50vh/5));
    grid-template-columns: repeat(4,calc(98.5vw/4));
    grid-gap: 0.8px;
}
.container div:first-child{
    grid-area: 1/1/2/3;
}
.container div{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: wheat;
    cursor: none;
}
.container div:hover{
    background-color: whitesmoke;
}
.container div:active {
    transform: scale(0.98);
  }

.calci h1{
    padding: 5px;
}